class Media {
	constructor() {
		// this.parentEle = document.body;
    }
	setVolume (ele: any, num:number){
		ele.volume = num;
	}
	setMuted (ele: any, onOFF: boolean){
		ele.muted = onOFF;
	}
	eventTester (ele: any, event: any, callback: () => void){    // 视频事件监测函数
        /*
         * eventTester("play");              // play()和autoplay开始播放时触发
         * eventTester("pause");             // pause() 暂停触发
         * eventTester("timeupdate");        // 播放时间改变
         * eventTester("ended");             // 播放结束
        self.eventTester(lnyvideo, "ended", function(){
            location.href = "/second";
            lnyvideo.pause(); 
            lnyvideo.style.height = "0";
        });
         */
        ele.addEventListener(event,function(){
             callback();
        },false);
    }
    // abstract init();
}

interface Options {
    parentEle: HTMLElement,
    path: string,
    volume?: number,
    isplay?: boolean,
    poster?: string
}

class Video extends Media {
	constructor() {
        this.posterEle = document.querySelector('.poster');
        this.videoEle = document.createElement('VIDEO');
		super();
    }
    init ({ parentEle = document.body, path, volume, isplay = false, poster }: Options){
        let self = this, parentEle = document.querySelector(parentEle);

        this.videoEle.setAttribute('src', path);
        
        // this.videoEle.setAttribute('preload', 'auto');
        this.videoEle.setAttribute('x5-playsinline', 'true');
        this.videoEle.setAttribute('playsinline', 'true');
        this.videoEle.setAttribute('webkit-playsinline', 'true');
        // this.videoEle.setAttribute('x5-video-player-type', 'h5');
        this.videoEle.poster = poster;
        this.videoEle.setAttribute('volume', volume);
        this.videoEle.defaultMuted = true;   // 设置是否静音
		// videoEle.setAttribute('controls', 'controls');
		parentEle.appendChild(this.videoEle);
		
        if(isplay) {
            this.videoEle.play();
            document.addEventListener("WeixinJSBridgeReady", function () {
                self.videoEle.play();
            }, false);  
        }


        this.eventTester(this.videoEle, 'play', function(){
            parentEle.style.height = '100%';
            // self.posterHide(); 
        })

        this.eventTester(this.videoEle, 'timeupdate', function(){
            //当视频的currentTime大于0.1时表示黑屏时间已过，已有视频画面，可以移除浮层（.pagestart的div元素）
            if ( !self.videoEle.isPlayed && self.videoEle.currentTime > 0.1 ){
                self.posterHide(); 
                self.videoEle.isPlayed = !0;
            } 
        })

        this.eventTester(this.videoEle, 'ended', function(){
            parentEle.style.height = 0;
            self.posterShow();

            self.posterEle.onclick = function(){
                return false;
            }
        });

		return this.videoEle;
    }
    posterShow(){
        this.posterEle.style.visibility = 'visible';
        this.videoEle.style.visibility = 'hidden';
    }
    posterHide(){
        this.posterEle.style.visibility = 'hidden';
        this.videoEle.style.visibility = 'visible';
    }
    
}

class Audio extends Media {
	constructor() {
		super();
    }
	init ({ parentEle = document.body, path, volume, isplay = false }: Options){
        const audioEle: any = document.createElement('AUDIO');
        audioEle.setAttribute('preload', 'auto');
		audioEle.setAttribute('src', path);
        audioEle.volume = volume;
		// audioEle.setAttribute('controls', 'controls');
		parentEle.appendChild(audioEle);
		if(isplay) {
            audioEle.play();
            document.addEventListener("WeixinJSBridgeReady", function () {
                audioEle.play();
            }, false);
        }
		return audioEle;
	}
}


export { Audio, Video }; 

